:root{
  --bgcolor:#000000;
  --text:#ffffff;
  --glass1:#ffffff35;
  --glass2:#ffffff25;
  --glass3:#ffffff05;
  --border:#ffffff7b;
  --error:red;
  --process:#ffffff7b;
  --success:green;
}

.App{
  text-align: center;
}

.shade{
  background:#00000050;
  width: 100vw;
  height: 100vh;
  z-index: -10;
}

*{
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

body{
  margin: 0rem;
  background-image: url('./img/wp2043502-computer-science-engineering-wallpapers.jpg') !important;
  background-repeat: no-repeat;
  background-size: cover;
  background-position:center;
  background-attachment: fixed;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

/* Glass panel */

.Glass-plate{
  background: -webkit-gradient(linear,right bottom, left top,color-stop(20%, var(--glass3)),color-stop(80%, var(--glass2)),color-stop(90%, var(--glass1)));
  background: -webkit-linear-gradient(bottom right,var(--glass3) 20%,var(--glass2) 80%,var(--glass1) 90%);
  background: -o-linear-gradient(bottom right,var(--glass3) 20%,var(--glass2) 80%,var(--glass1) 90%);
  background: linear-gradient(to top left,var(--glass3) 20%,var(--glass2) 80%,var(--glass1) 90%);
  -webkit-box-shadow: 1px 1px 5px 0px var(--bgcolor),0px 0px 0px 1px var(--border);
          box-shadow: 1px 1px 5px 0px var(--bgcolor),0px 0px 0px 1px var(--border);
  padding: 1rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 0.5rem;
  color:var(--text);
}


/* Alignment */

/* center */
.center{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

/* mobile center */
.mcenter{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

/* right */

.right-top{
  position: absolute;
  margin: auto;
  left: auto;
  top:0.5rem;
  right: 0.5rem;
}

.right-bottom{
  position: fixed;
  margin: auto;
  left: auto;
  top:auto;
  right: 0.5rem;
  bottom: 0.5rem;
  z-index: 100;
}

.left-top{
  position: absolute;
  margin: auto;
  left: 0.5rem;
  top:0.5rem;
}

.left-bottom{
  position: absolute;
  margin: auto;
  left: -1rem;
  top:auto;
  bottom: -1rem;
}

.fsize{
  font-size: 80% !important;
  text-decoration: underline;
}

.h1{
  font-size: 150% !important;
}

.h2{
  font-size: 130%;
}

.h3{
  font-size: 110%;
}

.red{
  color:var(--error) !important;
}

.bold{
  font-weight: bolder !important;
}

.margin{
  margin: 0.2rem !important;
}

.text-left{
  text-align: left;
}
.width{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
}

.bottom{
  margin-top: auto;
  margin-bottom: 0rem;
}

.flex-center{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

/* Width */
.fix-width{
  width: 18rem;
  margin: 0.5rem;
  margin-left: auto;
  margin-right: 0.5rem;
}

/* Hover */
.pop{
  -webkit-transform: scale(0.98);
      -ms-transform: scale(0.98);
          transform: scale(0.98);
}

.pop:hover{
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

/* Padding */
.padding{
  padding: 0.5rem;
}

/* Scroll bar */

::-webkit-scrollbar{
  width: 0px;
  /* background: snow; */
}

::-webkit-input-placeholder{
  color: var(--text);
}

::-moz-placeholder{
  color: var(--text);
}

::-ms-input-placeholder{
  color: var(--text);
}

::placeholder{
  color: var(--text);
}
.background{
  background: #000000 !important;
  padding: 0.5rem;
}

.unmargin{
  margin: -0.5rem !important;
}

.unpadding{
  padding: 0.8rem !important;
}

/* .margin{
  margin: 0.5rem !important;
} */

@media only screen and (max-width:426px) {
  .mcenter{
      position: relative;
      top:2rem;
      left: auto;
      -webkit-transform: none;
          -ms-transform: none;
              transform: none;
      /* overflow-x: hidden;
      overflow-y: auto;
      margin-top: 1rem;
      padding: 0.5rem 0rem; */
  }
  .fix-width{
    width: 100%;
  }
}


